import $ from 'jquery';
import 'slick-carousel';
import objectFitImages from 'object-fit-images';
import { WOW }  from 'wowjs';
import 'ScrollMagicVelocity';
import 'velocity';
import * as ScrollMagic from 'scrollmagic';

$(document).ready(function () {
  objectFitImages();

  var $status = $('.cover-slider__paging');
  var $slickElement = $('.cover-slider');

  $slickElement.on('init reInit afterChange', function(event, slick, currentSlide, nextSlide){
      var i = (currentSlide ? currentSlide : 0) + 1;
      $('.cover-slider__pagination--current').text(i);
      $('.cover-slider__pagination--total').text(slick.slideCount);
  });

  $slickElement.slick({
      autoplay: false,
      arrows: true,
      infinite: false,
  });

  var $clientsSlider = $('.clients__inner');
  var $clientsSliderPaging = $('.clients__pagination');

  $clientsSlider.on('init reInit afterChange', function(event, slick, currentSlide, nextSlide){
      var i = (currentSlide ? currentSlide : 0) + 1;
      $('.clients__pagination--current').text(i);
      $('.clients__pagination--total').text(slick.slideCount);
  });

  $(".header__nav a").click(function(e) {
    e.preventDefault();
    var aid = $(this).attr("href");
    $('html,body').animate({scrollTop: $(aid).offset().top},'slow');
});


  $clientsSlider.slick({
    slidesToShow: 5,
    slidesToScroll: 1,
    arrows: true,
  })
  
  $('.hamburger').click(function(){
    $(this).toggleClass('open');
    $('.header__nav').toggleClass('active');
  });

  
  setDotsLineSize();
  parallaxAnimation();
  $(window).resize(function() { 
    setDotsLineSize();
  });
  setActiveMenuItemOnScroll();
});

function setDotsLineSize() {
  var dots = $('.dot');
  var coverSize = $(window).width() - $('.menu-item__cover').offset().left + 25
  $('.cover__content').css('width', coverSize);
  $('.cover-slider__wrap').css('width', $(window).width() - coverSize)
  dots.each(function(index) {
    var parent = $(this).parent();
    var line = parent.find('.line')
    const leftOffset = parent.offset().left + parent.width() / 2
    $(this).css('left', leftOffset);
    line.css('height', $('.page').height() - 200)
    line.css('left', leftOffset + 5);
  })
  $('.menu-item__cover .line').css('height', $('#cover .rectangle').offset().top - 100);
}

function setActiveMenuItemOnScroll() {
  var topMenu = $(".nav"),
      topMenuHeight = topMenu.outerHeight() + 15,
      menuItems = topMenu.find("a"),
      scrollItems = menuItems.map(function() {
        var item = $($(this).attr("href"));
        if (item.length) { return item; }
      });

  const coverReactTop = $('#cover .rectangle').offset().top
  const credentials = $('#credentials').offset().top + $('#credentials').height() + 200;
  const team = $('#team').offset().top + $('#team').height() + 200;
  const partner = $('#partner').offset().top + $('#partner').height() + 200;
  const contact = $('#contact').offset().top + $('#contact').height() + 200;

  $(window).scroll(function() {
    var fromTop = $(this).scrollTop() + topMenuHeight;
    if (fromTop > coverReactTop - 80) {
      $('.menu-item__cover .line').hide()
      $('.menu-item__cover .dot').hide()
    } else {
      $('.menu-item__cover .line').show()
      $('.menu-item__cover .dot').show()
    }

    if (fromTop > credentials) {
      $('.menu-item__credentials .line').hide()
      $('.menu-item__credentials .dot').hide()
    } else {
      $('.menu-item__credentials .line').show()
      $('.menu-item__credentials .dot').show()
    }

    if (fromTop > team) {
      $('.menu-item__team .line').hide()
      $('.menu-item__team .dot').hide()
    } else {
      $('.menu-item__team .line').show()
      $('.menu-item__team .dot').show()
    }

    if (fromTop > partner) {
      $('.menu-item__partner .line').hide()
      $('.menu-item__partner .dot').hide()
    } else {
      $('.menu-item__partner .line').show()
      $('.menu-item__partner .dot').show()
    }

    if (fromTop > contact) {
      $('.menu-item__contact .line').hide()
      $('.menu-item__contact .dot').hide()
    } else {
      $('.menu-item__contact .line').show()
      $('.menu-item__contact .dot').show()
    }

    var cur = scrollItems.map(function(){
       if ($(this).offset().top < fromTop)
         return this;
     });
    cur = cur[cur.length-1];
    var id = cur && cur.length ? cur[0].id : "";
    menuItems
      .parent().removeClass("active")
      .end().filter("[href='#"+id+"']").parent().addClass("active");
  })
}

function parallaxAnimation() {
    // init controller
  var controller = new ScrollMagic.Controller();

  // create a scene
  var scene = new ScrollMagic.Scene({triggerElement: "#trigger-1", duration: $('#element-2').offset().top - $('#element-1').offset().top})
                .setPin("#element-1")
                .addTo(controller);

  var scene = new ScrollMagic.Scene({triggerElement: "#trigger-2"})
              .setVelocity("#element-1", {opacity: 0}, {duration: 0})
              .addTo(controller);

  var scene = new ScrollMagic.Scene({triggerElement: "#trigger-2", duration: $('#element-3').offset().top - $('#element-2').offset().top})
                .setPin("#element-2")
                .addTo(controller);

  var scene = new ScrollMagic.Scene({triggerElement: "#trigger-3"})
            .setVelocity("#element-2", {opacity: 0}, {duration: 0})
            .addTo(controller);

  var scene = new ScrollMagic.Scene({triggerElement: "#trigger-3", duration: $('.contact-form').offset().top - $('#element-3').offset().top + 20})
                .setPin("#element-3")
                .addTo(controller);

  var scene = new ScrollMagic.Scene({triggerElement: "#trigger-4", offset: 15, duration: $('.contact-form').offset().top - $('#element-4').offset().top + $(window).width() * (330 / 1920)})
                .setPin("#element-4")
                .addTo(controller);

  var scene = new ScrollMagic.Scene({triggerElement: "#trigger-5", offset: -$('#element-4').height() + 20 ,duration: $('.contact-form').offset().top - $('#element-5').offset().top + $('.contact-form').height() - $(window).width() * (192 / 1920) - 5})
                .setPin("#element-5")
                .addTo(controller);
}


